import code from '../../util/code.js';
import common from '../../util/common.js';
export default {
	data() {
		return {
			key: '',
			status: null,
			uuid: '',
			linkName:''
		};
	},
	onLoad(option) {
		this.uuid = uni.getStorageSync('userInfo').uuid;
		const that = this;
		that.key = option.key;
		//获取招聘信息
		uni.request({
			url: common.port.recruitmentById + '/' + option.key,
			method: 'GET',
			dataType: 'json',
			data: {},
			success: result => {
				console.log(result);
				if (result.data.code == 200) {
					that.status = result.data.data.applyStatus;
					that.linkName = result.data.data.recruitTheme;
				} else {
					uni.showToast({
						title: result.data.msg,
						icon: 'none',
						duration: 2000
					});
				}
			}
		});
	},
	methods: {
		start: function() {
			if (this.uuid) {
				uni.request({
					url: common.port.recruitmentById + '/' + this.key,
					method: 'GET',
					dataType: 'json',
					data: {},
					success: result => {
						if (result.data.code == 200) {
							if (result.data.data.applyStatus != 1) {
								uni.showToast({
									title: '该招聘未开始或已结束，请返回列表重新选择',
									icon: 'none',
									duration: 2000
								});
								setTimeout(function() {
									uni.navigateTo({
										url: '/pages/index/index'
									});
								}, 2000);
							} else {
								var data = 'recruitId=' + this.key + '&userId=' + this.uuid;
								uni.request({
									url: common.port.startApplyOrQuery + '?' + data,
									method: 'GET',
									dataType: 'json',
									data: {},
									success: res => {
										console.log(res);
										if (res.data.code == 200) {
											if (res.data.data) {
												if (res.data.data.isverify == 1) {
													uni.navigateTo({
														url: '/pages/signUpReview/signUpReview?key=' + this.key
													});
												} else if (res.data.data.isverify == 2) {
													uni.navigateTo({
														url: '/pages/signUpSuccess/signUpSuccess?key=' + this.key
													});
												} else if (res.data.data.isverify == 3) {
													uni.navigateTo({
														url: '/pages/signUpFail/signUpFail?key=' + this.key
													});
												} else if (res.data.data.isverify == 0) {
													uni.navigateTo({
														url: '/pages/notice/notice?key=' + this.key
													});
												}
											} else if (res.data.msg == null) {
												uni.navigateTo({
													url: '/pages/notice/notice?key=' + this.key
												});
											}
										} else {
											uni.showToast({
												title: res.data.msg,
												icon: 'none',
												duration: 2000
											});
										}
									}
								});
							}
						} else {
							uni.showToast({
								title: result.data.msg,
								icon: 'none',
								duration: 2000
							});
						}
					}
				});
			} else {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 2000
				});
			}
		},
		//已报名查询
		search() {
			if (this.uuid) {
				uni.request({
					url: common.port.recruitmentById + '/' + this.key,
					method: 'GET',
					dataType: 'json',
					data: {},
					success: result => {
						if (result.data.code == 200) {
							console.log(result.data)
							if (result.data.data.queryStatus != 1) {
								uni.showToast({
									title: '该招聘不在查询时间之内',
									icon: 'none',
									duration: 2000
								});
							} else {
								var data = 'recruitId=' + this.key + '&userId=' + this.uuid;
								uni.request({
									url: common.port.startApplyOrQuery + '?' + data,
									method: 'GET',
									dataType: 'json',
									data: {},
									success: res => {
										console.log(res);
										if (res.data.code == 200) {
											if (res.data.data) {
												if (res.data.data.isverify == 1) {
													uni.navigateTo({
														url: '/pages/signUpReview/signUpReview?key=' + this.key
													});
												} else if (res.data.data.isverify == 2) {
													uni.navigateTo({
														url: '/pages/signUpSuccess/signUpSuccess?key=' + this.key
													});
												} else if (res.data.data.isverify == 3) {
													uni.navigateTo({
														url: '/pages/signUpFail/signUpFail?key=' + this.key
													});
												} else if (res.data.data.isverify == 0) {
													uni.navigateTo({
														url: '/pages/signUpNodata/signUpNodata?key=' + this.key
													});
												}
											} else if (res.data.msg == null) {
												uni.navigateTo({
													url: '/pages/signUpNodata/signUpNodata?key=' + this.key
												});
											}
										} else {
											uni.showToast({
												title: res.data.msg,
												icon: 'none',
												duration: 2000
											});
										}
									}
								});
							}
						} else {
							uni.showToast({
								title: result.data.msg,
								icon: 'none',
								duration: 2000
							});
						}
					}
				});
			} else {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 2000
				});
			}
		},
		back: function(e) {
			uni.navigateBack({
				delta: 1
			});
		}
	}
};
